import React from 'react'

// import conponents
import Projects from '../components/Projects'

const Portfolio = () => {
    return (
        <section id='portfolio' className='section bg-primary min-h-[1400px]'>
            <div className=' container mx-auto'>
                <div className=' flex flex-col items-center text-3xl text-center'>
                    <h2 className=' selection:title before:content-portfolio relative before:absolute before:opacity-40
                    before:-top-[2] before:-left-3/4 before:hidden before:lg:block'> My latest work</h2>
                    <p className='subtitle text-amber-300'> Creating a masterpiece is simple.</p>
                </div>
                <Projects />
            </div>
        </section>
    )
}

export default Portfolio;